button {
  width: 7rem;
  height: 3rem;
  padding: 0.5rem 1.5rem;
  background-color: #3c74c9;
  color: white;
  border: 1px solid #4394d6;
  border-radius: 4px;
  cursor: pointer;
  font: inherit;
}

button:hover,
button:active {
  background-color: #2d0c7a;
  border-color: #080955;
}
